<template>
  <div id="login">
    <v-header title="注册登录"></v-header>
    <div class="control-wrapper position-absolute">
      <transition name="login-fade">
        <div v-show="pageSign === 'login'" class="login control-item position-absolute display-flex flex-center">
          <div class="account input-wrapper display-flex position-raletive">
            <span class="icon icon-user"></span>
            <input v-model="login.account" class="input phone" type="number" placeholder="请输入手机号">
          </div>
          <div class="password input-wrapper display-flex position-raletive">
            <span class="icon icon-key2"></span>
            <input v-model="login.password" class="input phone" type="password" placeholder="请输入密码">
          </div>
          <button @click="loginF" class="buttom">登录</button>
          <div class="to-other register"><span @click="changeControl('register')" class="text">前往注册</span>></div>
        </div>
      </transition>
      <transition name="register-fade">
        <div v-show="pageSign === 'register'" class="register control-item position-absolute display-flex flex-center">
          <div class="account input-wrapper display-flex position-raletive">
            <span class="icon icon-user"></span>
            <input v-model="register.account" class="input phone" type="number" placeholder="请输入手机号">
          </div>
          <div class="password input-wrapper display-flex position-raletive">
            <span class="icon icon-key2"></span>
            <input v-model.number="register.password" class="input phone" type="password" placeholder="请输入密码">
          </div>
          <div class="password input-wrapper display-flex position-raletive">
            <span class="icon icon-key2"></span>
            <input v-model.number="register.confirm" class="input phone" type="password" placeholder="确认密码">
          </div>
          <button @click="registerF" class="buttom">注册</button>
          <div class="to-other login"><<span @click="changeControl('login')" class="text">返回登录</span></div>
        </div>
      </transition>
    </div>
    <div class="copyright position-absolute">
      @Copyright 张大亨 2017~2027
    </div>
  </div>
</template>

<script>
  export default {
    name: 'login',
    data () {
      return {
        pageSign: 'login',
        login: {
          account: '',
          password: ''
        },
        register: {
          account: '',
          password: '',
          confirm: ''
        }
      }
    },
    methods: {
      changeControl (targetPage) {
        this.pageSign = targetPage
      },
      registerF () {
        if (this.register.account === '' || this.register.password === '' || this.register.confirm === '') {
          this.toast({iConClass: 'icon-cross', waiting: false, tip: '请填写完整'})
          return
        }
        if (this.register.password !== this.register.confirm) {
          this.toast({iConClass: 'icon-cross', waiting: false, tip: '两次密码不一致'})
          return
        }
        this.$http.post('http://192.168.31.200:3000/api/user/register', {phone: this.register.account, password: this.register.password}).then(response => {
          if (response.data.status !== 1) {
            this.toast({iConClass: 'icon-cross', waiting: false, tip: response.data.message})
            return
          }
          this.register = {
            account: '',
            password: '',
            confirm: ''
          }
          this.pageSign = 'login'
          this.toast({iConClass: 'icon-checkmark', waiting: false, tip: '注册成功'})
        }).catch(response => {
          this.toast({iConClass: 'icon-cross', waiting: false, tip: '发生错误'})
        })
      },
      loginF () {
        if (this.login.account === '' || this.login.password === '') {
          this.toast({iConClass: 'icon-cross', waiting: false, tip: '请填写完整'})
          return
        }
        this.$http.get('http://192.168.31.200:3000/api/user/login', {params: {phone: this.login.account, password: this.login.password}}).then(response => {
          if (response.data.status !== 1) {
            this.toast({iConClass: 'icon-cross', waiting: false, tip: response.data.message})
            return
          }
          this.saveUserInfo(response.data.value)
          this.toast({iConClass: 'icon-checkmark', waiting: false, tip: '登录成功'})
          this.$router.back()
        }).catch(response => {
          this.toast({iConClass: 'icon-cross', waiting: false, tip: '发生错误'})
        })
      },
      saveUserInfo (userInfo) {
        this.$store.commit('saveUserInfo', userInfo)
      }
    }
  }
</script>

<style lang="scss" scoped>
#login {
  width: 100%;
  min-height: 100%;
  position: relative;
  .control-wrapper {
    top: 50px;
    right: 10px;
    bottom: 50px;
    left: 10px;
    .control-item {
      flex-direction: column;
      top: 0;
      left: 0;
      width: 100%;
      height: 320px;
    }
    .input-wrapper {
      width: 200px;
      height: 50px;
      align-items: center;
      &::after {
        position: absolute;
        content: " ";
        right: -10px;
        bottom: 9px;
        left: 30px;
        height: 1px;
        background-color: #CCCCCC;
        transform: scaleY(0.5);
      }
      .icon {
        font-size: 28px;
        width: 30px;
      }
      .input {
        height: 28px;
        padding-left: 10px;
        flex: 1;
      }
    }
    .buttom {
      width: 170px;
      height: 28px;
      margin-top: 20px;
    }
    .to-other {
      width: 220px;
      margin-top: 20px;
      &.register {
        text-align: right;
      }
      &.login {
        text-align: left;
      }
      .text {
        text-decoration: underline;
      }
    }
  }
  .copyright {
    right: 0;
    bottom: 0;
    left: 0;
    height: 50px;
    line-height: 50px;
    text-align: center;
    &::before {
      position: absolute;
      content: " ";
      top: 0;
      right: 20px;
      left: 20px;
      height: 1px;
      background-color: #CCCCCC;
      transform: scaleY(0.5);
    }
  }
}

.login-fade-leave-active, .register-fade-leave-active {
  animation: rotate-leave .5s;
}
.login-fade-enter-active, .register-fade-enter-active {
  animation: rotate-enter .5s;
}

@keyframes rotate-leave {
  0% {
    transform: rotateY(0);
  }
  50% {
    transform: rotateY(90deg);
  }
  100% {
    transform: rotateY(90deg);
  }
}
@keyframes rotate-enter {
  0% {
    transform: rotateY(90deg);
  }
  50% {
    transform: rotateY(90deg);
  }
  100% {
    transform: rotateY(0);
  }
}
</style>



